.Container_Cabecera{
    display: flex;
    width: 100%;
    height: auto;
    justify-content: space-between;
    margin: 10px 0px;
    background: #22c1c3;  /* fallback for old browsers */
background: -webkit-linear-gradient(to top, #fdbb2d, #22c1c3);  /* Chrome 10-25, Safari 5.1-6 */
